html, body {
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: 100%;
}

body {
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 14pt;
  line-height: 20pt;
  color: #212121;
  -webkit-font-smoothing: antialiased;
  letter-spacing: -0.1pt;
}

a {
  text-decoration: none;
  color: #4183c4;
}
a:hover,a:focus,a:active{
  text-decoration:underline;
}

.header {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.pageBody {
  position: relative;
  padding: 0 36px;
  //z-index: 1;
  //background-color: #fff;
}

.contents {
  max-width: 740px;
  padding: 64px 0;
  margin: 0 auto;
}

.miniHeader {
  position: fixed;
  background: #6dbcdb;
  width: 100%;
  z-index: 1;
}

.miniHeaderContents {
  padding: 12px 36px;
  max-width: 740px;
  margin: 0 auto;
}

.miniHeaderContents a {
  text-decoration: none;
  color: #fff;
  text-shadow: 0 1px rgba(0,0,0,0.15);
  margin-right: 1em;
}

.miniHeaderContents a:last-child {
  margin-right: 0;
}

.miniLogo > .svg {
  height: 32px;
}


.cover {
  position: relative;
  width: 100%;
  height: 70vh;
  min-height: 260px;
  max-height: 800px;
  // background: #d7dadb;
  //z-index: 0;
  overflow: hidden;


  // left:0;
  // top:0;

  //position: fixed;
  // position: relative;
  z-index: 1;
  // width: 100%;
  // height: 100%;
  // height: 70vh;
  // min-height: 300px;
  // max-height: 800px;

  background-color: #d7dadb;
  outline: solid 1px rgba(0,0,0,0.2);


}

.coverFixed {
  height: 100%;
    display: -webkit-flex;
  display: flex;

  flex-direction: column;
  -webkit-flex-direction: column;

  justify-content: center;
  align-items: center;

  -webkit-justify-content: center;
  -webkit-align-items: center;

}

.filler {
  flex: 10;
  -webkit-flex: 10;
  width: 100%;
}

.synopsis {

  // position: absolute;
  // width: 100%;
  // height: 100%;
  position: relative;
  width: 100%;
  max-width: 700px;
  // height: 100px;
  // height: 100%;
  // max-height: 100px;
  // padding: 120px 30px;
  padding: 0 30px;
  box-sizing: border-box;

  flex: 10;
  -webkit-flex: 10;

}

.logo {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 60px;
  right: 60px;
}

.logo > .svg {
  position: absolute;
  width: 100%;
  height: 100%;
}



.buttons {
  flex: 10;
  -webkit-flex: 10;

  display: -webkit-flex;
  display: flex;

  flex-direction: row;
  -webkit-flex-direction: row;

  align-items: center;
  -webkit-align-items: center;


}

.buttons > .button {
  text-transform: uppercase;
  letter-spacing: 0.2pt;
  font-weight: 100;
  padding: 15px 30px;
  border-radius: 6px;
  //border: solid 1px rgba(0,0,0,0.1);
  cursor: pointer;
  width: 140px;
  text-align: center;
  background: #6dbcdb;//#2C3E50;
  color: #fff;//#2C3E50;//#FC4349;
}






