.github-btn {
  margin-top: -10%;
}

.gh-btn,.gh-count,.gh-ico{
  float: left;
}
.gh-btn,.gh-count{
  padding: 6px 14px 6px 12px;
  color: #212121;
  text-decoration: none;
  text-shadow: 0 1px 0 #fff;
  white-space: nowrap;
  cursor: pointer;
  font-size: 24px;
  line-height: 32px;
  font-weight: 300;
  border-radius: 6px;
}

.gh-btn{
  .gradient(#fafafa, #eaeaea);
  border:1px solid #d4d4d4;
  border-bottom-color:#bcbcbc
}
.gh-btn:hover,.gh-btn:focus,.gh-btn:active{
  color:#fff;
  text-shadow:0 -1px 0 rgba(0,0,0,.25);
  border-color:#518cc6 #518cc6 #2a65a0;
  background-color:#3072b3
}
.gh-btn:hover,.gh-btn:focus{
  .gradient(#599bdc, #3072b3);
}
.gh-btn:active{
  background-image:none;
  -webkit-box-shadow:inset 0 2px 5px rgba(0,0,0,.1);
  -moz-box-shadow:inset 0 2px 5px rgba(0,0,0,.1);
  box-shadow:inset 0 2px 5px rgba(0,0,0,.1)
}
.gh-ico{
  width: 28px;
  height: 28px;
  margin: 2px 6px 0 0;
  background-image:url(data:image/svg+xml;base64,);
  background-size:56px 28px;
  background-repeat:no-repeat;
  background-position:0 0
}
.gh-btn:hover .gh-ico,
.gh-btn:focus .gh-ico,
.gh-btn:active .gh-ico{
  background-position: -28px 0
}


.gh-count{
  display: block !important;
  position:relative;
  display:none;
  margin-left:10px;
  background-color:#fafafa;
  border:1px solid #d4d4d4
}
.gh-count:hover,.gh-count:focus{
  color:#4183C4
}
.gh-count:before,.gh-count:after{
  content:'';
  position:absolute;
  border-color:transparent;
  border-style:solid;
}
.gh-count:before{
  top: 50%;
  left: -7px;
  margin-top: -8px;
  border-width: 8px 8px 8px 0;
  border-right-color: #fafafa;
}
.gh-count:after{
  top:50%;
  z-index: -1;
  left: -8px;
  margin-top: -9px;
  border-width: 9px 9px 9px 0;
  border-right-color: #d4d4d4;
}


.markdown-body h1 {
  padding-bottom: 0.3em;
  font-size: 2.25em;
  line-height: 1.2;
  border-bottom: 1px solid #eee;
}

.markdown-body h2 {
  padding-bottom: 0.3em;
  font-size: 1.75em;
  line-height: 1.225;
  border-bottom: 1px solid #eee;
}

.markdown-body h3 {
  font-size: 1.5em;
  line-height: 1.43;
}




@media only screen and (max-width: 680px) {
  .gh-btn,.gh-count{
    padding: 4px 12px 4px 10px;
    font-size: 16px;
    line-height: 21px;
  }

  .gh-ico{
    width: 18px;
    height: 18px;
    // margin: 2px 6px 0 0;
    background-size:36px 18px;
  }
  .gh-btn:hover .gh-ico,
  .gh-btn:focus .gh-ico,
  .gh-btn:active .gh-ico{
    background-position: -18px 0
  }

// }



